
@import url('https://fonts.googleapis.com/css2?family=Comfortaa:wght@300;400;500;600;700&family=Josefin+Sans:wght@100&display=swap');
body {
    width: 100vw;
    height: 100vh;
    margin: 0;
    background-color: #282828;
    font-weight: 300;
}

/*--------------------------------------------------------------
#                      Scroll To Top
--------------------------------------------------------------*/


.scroll-to-top img{
    width: 3vmax;
    height: auto;
  }
  
  .scroll-to-top {
    position: fixed;
    bottom: 1vmax;
    right: 3%;
    z-index: 10;
    cursor: pointer;
    transform: scale(0.9);
    transition: all ease 0.4s;
  }
  .scroll-to-top:hover {
    transform: scale(1);
  }

.app {
    width: 100vw;
    height: 100vh;
    scroll-snap-type: y mandatory;
    overflow-y: scroll;
    overflow-x: hidden;
    font-family: 'Comfortaa', cursive;
}


div
.hero-section,
.sobre-section,
.projetos-section,
.contato-section {
    display: flex;
    scroll-snap-align: start;
}

/*--------------------------------------------------------------
#                      Header Geral
--------------------------------------------------------------*/

.header-section {
    position: fixed;
    display: flex;
    height: 11vmax;
    width: 99.55vmax;
    line-height: 17vmax;
    transition: all ease 0.3s;
    z-index: 2;
    user-select: none;
    margin-top: -9vmax;
    color: #f4f4f4;
    background-color: #0f0f0fb7;
}

.header-section:hover {
    background-color: #0f0f0f73;
    transform: translateY(8vmax);
}

.header-section:hover span {
    color: transparent;
}

/*--------------------------------------------------------------
#                       Logo Geral
--------------------------------------------------------------*/

.logo {
    margin-left: 7vmax;
    display: flex;
    justify-content: center;
    align-items: center;
    user-select: none;
    cursor: default;
  }

.btn-logo {
    cursor: pointer;
    line-height: 11vmax;
    margin-top: 0.2vmax;
    width: 20vmax;
    height: auto;
    transition: all ease 0.3s;
}
.btn-logo:hover {
    transform: translateX(0.7vmax);
}



