.hero-section {
    background-image: url(./components/background/image8.jpg);
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
    box-shadow: inset 0 0 0 100vh rgba(0, 0, 0, 0.35);
    justify-content: center;
    align-items: center;
    width: 100vw;
    height: 100vh;
    animation: change 55s infinite;
    display: flex;
    flex-direction: column;
}


@keyframes change {
    0%
    {
        background-image: url(./components/background/image8.jpg);
    }
    17%
    {
        background-image: url(./components/background/image2.png);
    }
    29%
    {
        background-image: url(./components/background/image3.png);
    }
    41%
    {
        background-image: url(./components/background/image6.png);
    }
    53%
    {
        background-image: url(./components/background/image7.png);
    }
    65%
    {
        background-image: url(./components/background/image1.png);
    }
    77%
    {
        background-image: url(./components/background/image10.png);
    }
    89%
    {
        background-image: url(./components/background/image11.png);
    }
}

